.about-top-div {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}



.header-image {
    width: 100%;
    margin-top: -2em;
    margin-bottom: 2em;
}

.about-profile-pic {
    float: left;
    margin: 0 1em 1em 0;
}




